<template>
    <div class='entry' data-v-facloseinput>
        <form data-v-facloseinput>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry></p>
                <input type="text" name="periodInput" v-model="bankPeriod">
            </div>
            <div class='entryInputBlock' data-v-facloseinput>
                <p data-v-faentry></p>
                <input type="text" name="status" v-model="status" readonly>
            </div>
        </form>
        <div class='entrySummary' data-v-facloseinput>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "refreshData"></div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "simulateClose" v-show="isOpen"></div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "executeClose" v-show="isOpen"></div>
            <div class="buttonBase compButton" data-v-facloseinput v-on:click = "reOpen" v-show="enableRepoen"></div>
        </div>
        <Modal
            v-if="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "posterrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>{{errorMsg}}</p>
                <p>({{errorCode}})</p>
            </template>
        </Modal>
        <Modal
            v-if="completeModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p>{{successMsg}}</p>
                <p>{{endBalance}}</p>
            </template>
        </Modal>

        <Loading v-if="loading" />
    </div>
    
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'

export default {
    name : 'FAcloseInput',

    props:['bid', 'bankPeriod'],
    mixins : [apiUrls],

    // ------------------------------------
    //  
    // ------------------------------------
    components : {
        Modal,
        Loading
    },

    data(){
        return{
            period : null,
            status : null,
            isOpen : false,
            enableRepoen : false,

            preBID : null,

            errorModal: false,
            errorCode : '',
            errorMsg  : '',

            successMsg : '',
            endBalance : 0,

            completeModal : false,
            loading : false
        }
    },
    

    // ------------------------------------
    //  
    // ------------------------------------
    updated()
    {
        if(this.bid == null || this.bid == this.preBID)
        {
            return
        }
        this.preBID = this.bid
                
        this.getBankCond()
    },
    
    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:
    {
        // ------------------------------------
        //  
        // ------------------------------------
        executeClose(){
            // 
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = 'YYYY/MM'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // period
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // this
            const self = this

            this.loading = true
            axios.post(this.apiUrls["close"], 
                {
                    bank  : this.bid,
                    year  : year,
                    month : month
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.successMsg     = ""
                        self.endBalance     = response.data.data.endbalance
                        self.completeModal  = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorMsg   = ''
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
                self.errorMsg   = 'aliss<br>'
                self.errorCode  = '9999'
            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = ''
                self.errorCode  = '9999'
                self.errorModal = true
            })
        },

        // ------------------------------------
        //  
        // ------------------------------------
        simulateClose(){
            // 
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = 'YYYY/MM'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // period
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // this
            const self = this

            this.loading = true
            axios.post(this.apiUrls["close"], 
                {
                    bank  : this.bid,
                    year  : year,
                    month : month,
                    simulate : true
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.successMsg     = ""
                        self.endBalance     = response.data.data.endbalance
                        self.completeModal  = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorMsg   = ''
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
                self.errorMsg   = 'aliss<br>'
                self.errorCode  = '9999'
            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = ''
                self.errorCode  = '9999'
                self.errorModal = true
            })
        },

        // ------------------------------------
        //  
        // ------------------------------------
        reOpen(){
        },

        // ------------------------------------
        //  
        // ------------------------------------
        refreshData(){
            this.getBankCond()
        },

         // ------------------------------------
        //  
        // ------------------------------------
        postModal(){
            this.completeModal = false
            location.reload()
            window.scrollTo(0,0)
            return
        },

        // ------------------------------------
        //  
        // ------------------------------------
        posterrorModal(){
            this.errorModal = false
            return
        },

        // ------------------------------------
        //  
        // ------------------------------------
        getBankCond(){            
            // 
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }
            
            if(this.bankPeriod.length != 7)
            {
                this.errorMsg   = 'YYYY/MM'
                this.errorCode  = '9999'
                this.errorModal = true
                return
            }

            // period
            let year  = Number(this.bankPeriod.substr(0,4))
            let month = Number(this.bankPeriod.substr(5,2))

            // this
            const self = this

            // BankAPI call
            let url = this.apiUrls["getBankCond"] + '/' + this.bid + '/' + year + '/' + month

            this.loading = true
            axios.get(url,
            {
                headers : {
                    'Authorization' : token
                }
            })
            .then(function(response){
                // Http status not 200
                if(response.status != 200)
                {
                    self.loading    = false
                    self.errorMsg   = 'aliss<br>'
                    self.errorCode  = '9999'
                    self.errorModal = true
                    return
                }
                // Auth failed
                if(response.data.status != 'S')
                {
                    self.loading    = false
                    self.errorMsg   = ''
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }

                // Open/Close
                if(response.data.data.status == 0)
                {
                    self.status = 'Open'
                    self.isOpen = true
                }
                else
                {
                    self.status = 'Closed'
                    self.isOpen = false
                }

                // 
                self.enableRepoen = response.data.data.enableRepoen

                self.loading    = false

            })
            .catch(function(error){
                self.loading    = false
                self.errorMsg   = ''
                self.errorCode  = '9999'
                self.errorModal = true
                return
            })

        }
    }

}
</script>


<style scoped>
    .entry[data-v-facloseinput]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }
    .entryInputBlock[data-v-facloseinput]{
        float:left;
        padding:5px;
    }
    .entrySummary[data-v-facloseinput]{
        clear:left;
        padding:5px;
    }

    .compButton[data-v-facloseinput]{
        margin : 15px 0px;
    }

</style>


