<template>
    <section data-v-faclose>
        <!--  -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 

        <!--  -->
        <FAheader 
            v-bind:header = 'faHeader'
        />

        <!--  -->
        <FAcloseInput 
            v-bind:bid = 'faHeader.bank'
            v-bind:bankPeriod = 'faHeader.bankPeriod'
        />
    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAcloseInput from '../components/FAcloseInput'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'


export default {
    name : 'FAclose',
    mixins : [apiUrls],
    
    // ------------------------------------
    //  
    // ------------------------------------
    components : {
        ViewHeader,
        FAheader,
        FAcloseInput,
        Modal,
        Loading
    },

    // ------------------------------------
    //  
    // ------------------------------------
    created : function(){

        // FA
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()
    },

    // ------------------------------------
    //  
    // ------------------------------------
    data(){
        return {
            moduleName : 'FA',
            viewName : '',

            // ------------------------------------
            // FA
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10,
                bankPeriod : null
            },

            // // BankId
            // bid : 0,
            // bankPeriod1 : null
        }
    },

    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
    }

}
</script>


