<template>
    <section data-v-fainput>
        <!--  -->
        <ViewHeader 
            v-bind:moduleName   =   'moduleName'
            v-bind:viewName     =   'viewName'
        /> 

        <!--  -->
        <FAheader 
            v-bind:header = 'faHeader'
        />

        <!--  -->
        <div class='buttonBase fainputButton' data-v-fainput></div>

        <!--  -->
        <FAentry
            v-for='(entry, index) in faEntrys' :key='index'
            v-if='index > 0'
            v-bind:index = 'index'
            v-bind:entry = 'entry'
            v-bind:accId = 'accId'
            v-on:entry-input-event = 'entryUpdate'
            v-on:entry-del-event = 'delEntry'
        />

        <!--  -->
        <div class='buttonBase fainputButton' data-v-fainput v-on:click = 'addEntry'></div>

        <!--  -->
        <section>
            <div class='entry' data-v-fainput>
                <form data-v-fainput>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput></p>
                        <input type='number' name='hExtInput' v-model="faHvalue.hExt">
                    </div>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput></p>
                        <input type='number' name='hDiscountInput' v-model="faHvalue.hDiscount">
                    </div>
                    <div class='entryInputBlock' data-v-fainput>
                        <p class='formLabel' data-v-fainput></p>
                        <input type='text' name='hCommentInput' class='commentInput' v-model="faHvalue.hComment" data-v-fainput>
                    </div>
                </form>
            </div>
        </section>

        <!--  -->
        <section>
            <div class='entry' data-v-fainput>
                <table  data-v-fainput>
                    <tr><td></td><td>{{ total }}</td></tr>
                    <tr><td></td><td>{{ faHeader.balance }}</td></tr>
                    <tr><td></td><td>{{ afterBalance }}</td></tr>
                </table>
            </div>
        </section>

        <!--  -->
        <div class='buttonBase fainputButton' v-on:click = "registor" data-v-fainput></div>

        <!--  -->
        <Modal
            v-show="confirmModal"
            v-bind:mtypeIn='1'
            v-on:modal-yes-event    = "confirmYes"
            v-on:modal-no-event     = "confirmNo"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <table  data-v-fainput>
                    <p></p>
                    <tr><td></td><td>{{ total }}</td></tr>
                    <tr><td></td><td>{{ faHeader.balance }}</td></tr>
                    <tr><td></td><td>{{ afterBalance }}</td></tr>
                </table>
            </template>
        </Modal>

        <!--  -->
        <Modal
            v-show="completeModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                
            </template>
        </Modal>

        <!--  -->
        <Modal
            v-show="errorModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event = "postErrorModal"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p></p>
                <p>({{errorCode}})</p>
            </template>
        </Modal>

        <Loading v-show="loading" />

    </section>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import apiUrls  from '../mixins/ApiUrlMixin'

import ViewHeader   from '../components/ViewHeader'
import FAheader     from '../components/FAheader'
import FAentry      from '../components/FAentry'
import Modal        from '../components/Modal'
import Loading      from '../components/Loading'

export default {
    name : 'FAinput',
    mixins : [apiUrls],
    
    // ------------------------------------
    //  
    // ------------------------------------
    components : {
        ViewHeader,
        FAentry,
        FAheader,
        Modal,
        Loading
    },

    // ------------------------------------
    //  
    // ------------------------------------
    created(){
        // FA
        let dt = new Date()
        this.faHeader.year  = dt.getFullYear()
        this.faHeader.month = dt.getMonth()+1
        this.faHeader.day   = dt.getDate()

        //  
        // 
        let token = Cookies.get('token')
        if(token == null)
        {
            return
        }

        // this
        const self = this

        // API call
        axios.get(this.apiUrls["getAccounts"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                return
            }
            
            // 
            for(let i=0; i<response.data.data.length; i++)
            {
                // 
                let data = Object.assign({}, self.accId[0])
                
                // 
                data.id     = response.data.data[i].accid
                data.name   = response.data.data[i].name
                data.type   = response.data.data[i].accType
                // 
                self.accId.push(data)
            }

            // 
            self.accId.shift()
        })
        .catch(function(error){
            return
        })
    },

    data(){
        return {
            moduleName : 'FA',
            viewName : '',

            // ------------------------------------
            // FA
            // ------------------------------------
            faHeader: {
                year : null,
                month : null,
                day : null,
                bank : '',
                balance : 10,
                bankPeriod : null
            },

            // ------------------------------------
            // FA
            // ------------------------------------
            faEntrys: [
                {
                    accId: '',
                    entryTxt: '',
                    value: null,
                    discount: null,
                    ext: null,
                    remarks: '',
                    entrySum: null,
                    entryAgg : null
                },
                {
                    accId: '',
                    entryTxt: '',
                    value: null,
                    discount: null,
                    ext: null,
                    remarks: '',
                    entrySum: null,
                    entryAgg : null
                }
            ],

            // ------------------------------------
            //  
            // ------------------------------------
            faHvalue : {
                hExt : null,
                hDiscount : null,
                hComment : ''
            },

            // ------------------------------------
            // 
            // ------------------------------------
            accId : [
                {
                    id      : "",
                    name    : "" ,
                    type    : null
                }
            ],

            //  
            confirmModal : false,

            completeModal : false,
            errorModal    : false,

            loading : false,

            errorCode : ''
        }
    },

    computed : {
        // ------------------------------------
        // 
        // ------------------------------------
        total(){
            let total = 0

            //  
            for (var i = 1; i < this.faEntrys.length; i++) {
                total += this.faEntrys[i].entrySum;
            }

            //  
            total = total - Number(this.faHvalue.hExt) + Number(this.faHvalue.hDiscount)

            return total
        },

        // ------------------------------------
        //  
        // ------------------------------------
        afterBalance(){
            return this.faHeader.balance + this.total
        }
    },

    // ------------------------------------
    //  Methods
    // ------------------------------------
    methods:{
        // ------------------------------------
        // 
        // ------------------------------------
        entryUpdate: function (retObj) {
            // 
            this.faEntrys[retObj.index].entrySum = retObj.total
        },

        // ------------------------------------
        // 
        // ------------------------------------
        addEntry : function(){
            // 
            let faEntry = Object.assign({}, this.faEntrys[0])
            // 
            this.faEntrys.push(faEntry)
        },

        // 
        delEntry : function(index){
            this.$delete(this.faEntrys, index)
        },

        // ------------------------------------
        // 
        // ------------------------------------
        registor : function()
        {
            // this.callPostFaData()

            // 
            this.confirmModal = true
        },

        // ------------------------------------
        //  Yes
        // ------------------------------------
        confirmYes (){
            // 
            this.confirmModal = false
            // loading
            this.loading = true

            // API
            this.callPostFaData()
        },

        // ------------------------------------
        //  No
        // ------------------------------------
        confirmNo (){
            // 
            this.confirmModal = false
        },

        // ------------------------------------
        // API
        // ------------------------------------
        callPostFaData(){
            let res = {
                    header : {
                        year    : Number(this.faHeader.year),
                        month   : Number(this.faHeader.month),
                        day     : Number(this.faHeader.day),
                        bank    : this.faHeader.bank,
                        hExt    : Number(this.faHvalue.hExt),
                        hDiscount: Number(this.faHvalue.hDiscount),
                        hComment: this.faHvalue.hComment,
                        hTotal  : this.total
                    },
                    entries : this.faEntrys
                }
            console.log(JSON.stringify(res))


            const self   = this

            // 
            let token = Cookies.get('token')
            if(token == null)
            {
                return
            }

            // API call
            axios.post(this.apiUrls["postFaData"], 
                {
                    header : {
                        year    : Number(this.faHeader.year),
                        month   : Number(this.faHeader.month),
                        day     : Number(this.faHeader.day),
                        bank    : this.faHeader.bank,
                        hExt    : Number(this.faHvalue.hExt),
                        hDiscount: Number(this.faHvalue.hDiscount),
                        hComment: this.faHvalue.hComment,
                        hTotal  : this.total
                    },
                    entries : this.faEntrys
                },
                {
                    headers : {
                        'Authorization' : token
                    }
                }
            )
            .then(function(response){
                self.loading    = false
                // HTTP call success
                if(response.status == 200)
                {
                    // API success
                    if(response.data.status == 'S')
                    {
                        self.completeModal = true
                        return
                    }
                    // API error
                    self.errorCode  = response.data.code
                    self.errorModal = true
                    return
                }
                // HTTP error
                self.errorModal = true
            })
            .catch(function(error){
                self.loading    = false
                self.errorModal = true
            })
        },

        // ------------------------------------
        // OK
        // ------------------------------------
        postModal(){
            this.completeModal = false
            location.reload()
            window.scrollTo(0,0)
            return
        },
        postErrorModal(){
            this.errorModal = false
            return
        }
    }
}
</script>

<style scoped>
    .fainputButton[data-v-fainput]{
        /* width:100px; */
        margin : 30px 15px;
    }

    .entry[data-v-fainput]{
        border: solid 1px #AAAAAA;
        overflow: hidden;
        padding:10px;
        margin: 20px 0px;
        box-shadow: 0 0 6px gray;
    }

    .entryInputBlock[data-v-fainput]{
        float:left;
        padding:5px;
    }

    p.formLabel[data-v-fainput]{
        margin-top:10px;
        margin-bottom: 5px;
    }

    input.commentInput[data-v-fainput]{
        width:250px;
    }

    table[data-v-fainput]{
        margin: 10px 0px;
    }
</style>





