// 
const _dev = false

// URL
const _apiUrlsDev = {
    login :     "http://localhost:3000/api/v1/auth",
    logout:     "http://localhost:3000/api/v1/user/logout",
    authToken : "http://localhost:3000/api/v1/user",
    config:     "http://localhost:3000/api/v1/config/",

    // FA module
    getBanks:    "http://localhost:3000/api/v1/fa/banks",
    postFaData:  "http://localhost:3000/api/v1/fa/fadata",
    getAccounts: "http://localhost:3000/api/v1/fa/accounts?category=1",
    getAllAccounts: "http://localhost:3000/api/v1/fa/accounts",
    getBankCond: "http://localhost:3000/api/v1/fa/bankCond",
    close:       "http://localhost:3000/api/v1/fa/closePeriod",
    transfer:    "http://localhost:3000/api/v1/fa/transfer"
}

// URL
const _apiUrlsPrd = {
    login :     "https://aliss-app.herokuapp.com/api/v1/auth",
    logout:     "https://aliss-app.herokuapp.com/api/v1/user/logout",
    authToken : "https://aliss-app.herokuapp.com/api/v1/user",
    config:     "https://aliss-app.herokuapp.com/api/v1/config/",

    // FA module
    getBanks:    "https://aliss-app.herokuapp.com/api/v1/fa/banks",
    postFaData:  "https://aliss-app.herokuapp.com/api/v1/fa/fadata",
    getAccounts: "https://aliss-app.herokuapp.com/api/v1/fa/accounts?category=1",
    getAllAccounts: "https://aliss-app.herokuapp.com/api/v1/fa/accounts",
    getBankCond: "https://aliss-app.herokuapp.com/api/v1/fa/bankCond",
    close:       "https://aliss-app.herokuapp.com/api/v1/fa/closePeriod",
    transfer:    "https://aliss-app.herokuapp.com/api/v1/fa/transfer"
}

export default {
    data(){
        // 
        if(_dev){
            return {
                apiUrls: _apiUrlsDev
            }
        }
        // 
        else{
            return {
                apiUrls: _apiUrlsPrd
            }
        }

    }
}


